    width: 100%;
    height: 100%;
    overflow: auto;
}
.chrome-tip-warn {
    display: block;
    width: 160px;
    height: 160px;
    margin: 200px auto 16px;
}
.chrome-tip-text {
    font-size: 40px;
    text-align: center;
    color: rgba(0, 0, 0, 0.85);
    line-height: 56px;
}
.chrome-hack-text {
    position: relative;
    margin-top: 16px;
    font-size: 32px;
    text-align: center;
    color: rgba(0, 0, 0, 0.65);
    line-height: 45px;
}
.chrome-hack-text::after {
    content: '';
    position: absolute;
    top: 85px;
    left: 50%;
    transform: translateX(-50%);
    width: 57%;
    height: 1px;
    background: rgba(0, 0, 0, 0.15);
}
.chrome-download-text {
    margin-top: 81px;
    font-size: 24px;
    text-align: center;
    color: rgba(0, 0, 0, 0.85);
    line-height: 33px;
}
.chrome-icon {
    display: block;
    width: 52px;
    height: 52px;
    margin: 24px auto;
}
.chrome-download-btn {
