    width: 160px;
    height: 48px;
    margin: 0 auto;
    background: #3585F9;
    border-radius: 8px;
    font-size: 16px;
    color: #FFFFFF;
    text-align: center;
    line-height: 48px;
}
.chrome-enable-text {
    margin-top: 24px;
    font-size: 16px;
    text-align: center;
    color: rgba(0, 0, 0, 0.45);
    line-height: 22px;
}
